import React from "react";
import "./MapCard.css";
import { Fade } from "react-reveal";

export default function MapCard({ map, theme }) {
  function openMapinNewTab(url) {
    var win = window.open(url, "_blank");
    win.focus();
  }

  return (
    <div className="map-card-div" style={{ backgroundColor: theme.splashBg }}>
      <Fade bottom duration={2000} distance="40px">
        <div key={map.id} onClick={() => openMapinNewTab(map.url)}>
          <div className="map-name-div">
            <p className="map-name" style={{ color: theme.white }}>
              {map.name}
            </p>
          </div>
          <p className="map-description" style={{ color: theme.white }}>
            {map.description}
          </p>
          <div className="map-details">
            <p
              className="map-creation-date subTitle"
              style={{ color: theme.white }}
            >
              {map.createdAt.split("T")[0]}
            </p>
          </div>
          <div>
            <img src={map.image} className="map-image" />
          </div>
          {}
        </div>
      </Fade>
    </div>
  );
}
